.highlighted-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 10vmin;
  border-radius: 10vmin;
  border: 1vmin solid #da9400;
  font-family: Montserrat Black;
  color: #F5C21B;
  background-color: #103769;
  background: linear-gradient(150deg, #194f94 0%, #03162f 100%);
  transition-property: border-color, color, background-color, box-shadow, text-shadow, transform;
  transition-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
  transition-duration: 0.5s;
  will-change: text-shadow, box-shadow;
  overflow: hidden;
}
@keyframes selected {
  from {
    left: 0;
    transform: translateX(-100%);
  }
  to {
    left: 100%;
    transform: translateX(0);
  }
}
.highlighted-content::before {
  pointer-events: none;
  z-index: 1;
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, white 25%, white 75%, rgba(255, 255, 255, 0) 100%);
  transform: translateX(-100%);
  opacity: 0.1;
}
.highlighted-content h2 {
  margin: 2vmin 4vmin;
  font-style: italic;
  font-size: 3vmin;
  text-transform: uppercase;
}
.highlighted-content:hover, .highlighted-content.active {
  z-index: 3;
  transform: scale(1.2);
  text-shadow: 0 0 2vmin rgba(245, 194, 27, 0.3), 0 0.1vmin 0 #966508, 0 0.2vmin 0 #966508, 0 0.3vmin 0 #966508, 0 0.4vmin 0 #966508, 0 0.5vmin 0 #966508;
  box-shadow: inset 0 0 0.1vmin #03162f, 0 0.1vmin 0 #966508, 0 0.2vmin 0 #966508, 0 0.3vmin 0 #966508, 0 0.4vmin 0 #966508, 0 0.5vmin 0 #966508, 0 0 0.5vmin rgba(255, 255, 255, 0.5);
}
.highlighted-content:hover::before, .highlighted-content.active::before {
  animation: selected 1s infinite;
}
.highlighted-content.active {
  transform: scale(1);
}