@charset "UTF-8";
.replays {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-grow: 0;
  flex-shrink: 0;
  width: 14vmin;
  height: 10vmin;
  border-radius: 10vmin;
  border: 1vmin solid #da9400;
  font-family: Montserrat Black;
  color: #F5C21B;
  text-transform: uppercase;
  text-align: center;
  background-color: #103769;
  background: linear-gradient(150deg, #194f94 0%, #03162f 100%);
  transition-property: border-color, color, background-color, text-shadow, box-shadow, transform;
  transition-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
  transition-duration: 0.5s;
}
.replays .date {
  pointer-events: none;
  width: 100%;
  font-size: 1.6vmin;
}
.replays .arrow,
.replays .launch {
  cursor: pointer;
  z-index: 2;
  position: absolute;
  width: 4vmin;
  height: 4vmin;
  border-radius: 2vmin;
  background-color: transparent;
  background-color: #da9400;
  opacity: 0;
  transition-property: opacity;
  transition-duration: 0.3s;
}
.replays .arrow::before,
.replays .launch::before {
  content: "⇧";
  position: absolute;
  display: block;
  top: 50%;
  left: 50%;
  font-size: 2vmin;
  color: #000;
  transform: translate(-50%, -50%);
}
.replays .arrow.previous,
.replays .launch.previous {
  left: -2.5vmin;
  transform: rotateZ(-90deg);
}
.replays .arrow.next,
.replays .launch.next {
  right: -2.5vmin;
  transform: rotateZ(90deg);
}
.replays .launch {
  opacity: 1;
  top: 100%;
  left: 50%;
  background-color: #103769;
  transform: translate(-50%, -25%);
}
.replays .launch::before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  background-image: url("@/images/play.svg");
  background-size: 50% auto;
  background-position: center center;
  background-repeat: no-repeat;
  filter: invert(100%);
}
.replays:hover {
  transform: scale(1.2);
  text-shadow: 0 0 2vmin rgba(245, 194, 27, 0.3), 0 0.1vmin 0 #966508, 0 0.2vmin 0 #966508, 0 0.3vmin 0 #966508, 0 0.4vmin 0 #966508, 0 0.5vmin 0 #966508;
  box-shadow: inset 0 0 1vmin #03162f, 0 0.1vmin 0 #966508, 0 0.2vmin 0 #966508, 0 0.3vmin 0 #966508, 0 0.4vmin 0 #966508, 0 0.5vmin 0 #966508, 0 0 5vmin rgba(255, 255, 255, 0.5);
}
.replays:hover .arrow,
.replays:hover .launch {
  opacity: 1;
}
.replays:hover .arrow:hover,
.replays:hover .launch:hover {
  background-color: #FFF;
  box-shadow: 0 0 2vmin #FFF;
}
.replays:hover .launch:hover::before {
  filter: none;
}