.launcher {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 2vmin 0;
  height: 12vmin;
}
@media (max-width: 960px) {
  .launcher {
    width: 100%;
    align-self: flex-start;
  }
}
.launcher .start.with-replay {
  margin-right: 4vmin;
}
@media (max-width: 960px) {
  .launcher .start {
    width: 100%;
  }
}
.launcher .start .buttons {
  flex-grow: 0;
  flex-shrink: 0;
  height: 8vmin;
}
.launcher .start .buttons .launch {
  display: inline-block;
  cursor: pointer;
  margin: 0 1vmin;
  border-radius: 10vmin;
  width: 8vmin;
  height: 8vmin;
  background-color: #F5C21B;
  transition-property: box-shadow, transform, filter;
  transition-duration: 0.2s;
}
.launcher .start .buttons .launch::before {
  pointer-events: none;
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  background-size: 50% auto;
  background-position: center center;
  background-repeat: no-repeat;
  filter: drop-shadow(0 0 0.2vmin #FFF);
}
.launcher .start .buttons .launch.current::before {
  background-image: url("@/images/play.svg");
}
.launcher .start .buttons .launch.replay::before {
  background-image: url("@/images/replay.svg");
}
.launcher .start:hover .launch {
  box-shadow: 0 0 2vmin rgba(245, 194, 27, 0.3), 0 0.1vmin 0 #966508, 0 0.2vmin 0 #966508, 0 0.3vmin 0 #966508, 0 0.4vmin 0 #966508, 0 0.5vmin 0 #966508;
  transform: translateY(-0.5vmin);
}
.launcher .start:hover .launch:hover {
  background: #FFF;
  box-shadow: 0 0 2vmin rgba(245, 194, 27, 0.3), 0 0.2vmin 0 #FFF, 0 0.4vmin 0 #FFF, 0 0.6vmin 0 #FFF, 0 0.8vmin 0 #FFF;
  transform: translateY(-0.8vmin);
}
.launcher .start:hover:active .launch:hover {
  box-shadow: 0 0 2vmin rgba(245, 194, 27, 0.3);
  transform: none;
}