canvas {
  display: block;
  width: 100vw;
  height: 100vh;
}

.main-nav {
  z-index: 2;
  display: flex;
  flex-direction: column;
  position: absolute;
  justify-content: center;
  box-sizing: border-box;
  left: 0;
  top: 0;
  padding: 5vw;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7);
  transition: opacity 1s;
}

.launcher:nth-child(1) {
  align-self: flex-start;
}
.launcher:nth-child(2) {
  align-self: center;
}
.launcher:nth-child(3) {
  align-self: flex-end;
  margin-bottom: 20px;
}
.launcher:hover {
  z-index: 3;
}

.launched-nav {
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  font-family: Montserrat Black;
  text-transform: uppercase;
}
.launched-nav .back {
  position: absolute;
  left: 5vmin;
  top: 5vmin;
  padding: 0 30px;
  height: 60px;
  font-size: 2vmin;
  cursor: pointer;
}
.launched-nav .winning-prize,
.launched-nav .winning-number,
.launched-nav .winner {
  position: absolute;
  left: 50%;
  top: 20vh;
  font-size: 4vmax;
  transform: translateX(-50%);
}
.launched-nav .draw-results {
  flex-direction: column;
  box-sizing: border-box;
  position: absolute;
  left: 50%;
  top: 50%;
  padding: 50px;
  width: 80vmin;
  min-width: 450px;
  height: 40vmin;
  min-height: 300px;
  border-radius: 5vmin;
  transform: translate(-50%, -50%);
}
.launched-nav .draw-results .value {
  font-style: italic;
  font-size: 1.3em;
  color: #FFF;
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}